<template>
  <div class="panel">
    <h1 class="title"></h1>
    <el-tabs type="border-card" stretch v-model="currentTab">
      <el-tab-pane name="account">
        <template #label>
          <span class="icon">
            <el-icon><UserFilled /></el-icon>
            <span class="text"></span>
          </span>
        </template>
        <PanelAccount ref="accountRef" />
      </el-tab-pane>
      <el-tab-pane name="phone">
        <template #label>
          <span class="icon">
            <el-icon><Iphone /></el-icon>
            <span class="text"></span>
          </span>
        </template>
        <PanelPhone ref="phoneRef" />
      </el-tab-pane>
    </el-tabs>
    <div class="control-account">
      <el-checkbox v-model="isKeep" label="" />
    </div>
    <el-button type="primary" class="login-btn" @click="loginAction"></el-button>
  </div>
</template>

<script setup lang="ts" name="panel">
import { ref, watch } from 'vue'
import PanelPhone from './panel-phone.vue'
import PanelAccount from './panel-account.vue'
import { localCache } from '@/utils/cache'

const currentTab = ref('account')
// 
// const isKeep = ref<boolean>(localCache.getCache('rem_pwd'))
// : type 'string | null' is not assignable to parameter of type 'boolean'
// 
const isKeep = ref<boolean>(Boolean(localCache.getCache('rem_pwd')))
watch(isKeep, (newValue) => {
  // const val: any = newValue
  localCache.setCache('rem_pwd', newValue)
  // localStorage.setItem('hhh', newValue)
})

// <InstanceType<typeof Component>> 
// Component
//

// Component
//      

// class Person {}  const p = new Person()
//  typeof Person === InstanceType<typeof Person>
// Person
// PanelAccount""PanelAccount
// PanelAccount
// typeof Person InstanceType
const accountRef = ref<InstanceType<typeof PanelAccount>>()
const phoneRef = ref<InstanceType<typeof PanelPhone>>()

function loginAction() {
  if (currentTab.value === 'account') {
    accountRef.value?.loginAction(isKeep.value)
  } else {
    phoneRef.value?.loginAction(isKeep.value)
  }
}
</script>

