<template>
  <div class="account">
    <el-form :model="account" label-width="60px" ref="formRef" :rules="accountRules">
      <el-form-item label="" prop="name">
        <el-input v-model="account.name" />
      </el-form-item>

      <el-form-item label="" prop="password">
        <el-input v-model="account.password" show-password />
      </el-form-item>
    </el-form>
  </div>
</template>

<script setup lang="ts" name="account">
import { reactive, ref } from 'vue'
import { ElMessage } from 'element-plus'
import type { FormRules, FormInstance } from 'element-plus'
import useLoginStore from '@/store/login/login'
import { localCache } from '@/utils/cache'
import type { IAccount } from '@/types'

// 1.
const account = reactive<IAccount>({
  name: localCache.getCache('name') ?? '',
  password: localCache.getCache('password') ?? ''
})

// 2.form
const accountRules: FormRules = {
  name: [
    { required: true, message: '~', trigger: 'blur' },
    { pattern: /^[a-z0-9]{6,20}$/, message: '6~20', trigger: 'blur' }
  ],
  password: [
    { required: true, message: '~', trigger: 'blur' },
    { pattern: /^[a-z0-9]{3,}$/, message: '3', trigger: 'blur' }
  ]
}

// 3.
const formRef = ref<FormInstance>()
const loginStore = useLoginStore()
function loginAction(isKeep: boolean) {
  // 
  formRef.value?.validate((isValid) => {
    if (isValid) {
      const name = account.name
      const password = account.password
      // 1.
      loginStore.accountLoginAction({ name, password })

      // 2.
      if (isKeep) {
        localCache.setCache('name', name)
        localCache.setCache('password', password)
      } else {
        localCache.removeCache('name')
        localCache.removeCache('password')
      }
    } else {
      ElMessage.warning({ message: '~' })
    }
  })
}

// 4.
defineExpose({
  loginAction
})
</script>

<style scoped lang="less"></style>
